.Login{padding:20px;max-width:500px;width:100%;backdrop-filter:blur(5px);background-color:rgba(255,200,94,.9);border-radius:20px;box-shadow:0 0 5px 2px #ffc85e;min-height:500px;margin-bottom:50px;margin-top:50px}.Login .Message{text-align:center}.Login form{display:flex;flex-direction:column;gap:50px;justify-content:center;height:100%;max-width:70%;font-weight:500;font-size:18px}.Login form h2{width:70%;margin:0 auto;color:#fff;text-align:center;font-size:40px;margin-bottom:-20px}.Login form a{color:#fff;text-align:center;width:70%;margin:0 auto;display:block;margin-top:15px;transition:.3s}.Login form a:hover,.Login form a:active{text-shadow:0 0px 5px #fff}.Login form input{margin:0 auto;height:50px;display:flex;flex-direction:column;font-size:18px;margin-top:5px}.Login form input::placeholder:after{content:"  ✖"}.Login form input:active,.Login form input:focus{box-shadow:0 0 0px 5px rgba(255,255,255,.5)}.Login form button{margin:0 auto;width:70%}.Login form label{margin:0 auto;width:70%;display:block}.Login form input[type=submit]{width:70%;display:flex;justify-content:center;align-items:center}.Login form p{width:70%;margin:0 auto;padding:0}